export class Slice {
    uint8;
    view;
    start;
    end;
    constructor(uint8, view, start, end) {
        this.uint8 = uint8;
        this.view = view;
        this.start = start;
        this.end = end;
    }
    subarray() {
        return this.uint8.subarray(this.start, this.end);
    }
}
